/* src/components/Footer.css */
.footer {
     background-color: #333;
     color: #fff;
     padding: 20px 0;
     text-align: center;
     position: fixed;
     bottom: 0;
     width: 98%;
     border-radius: 5px;
}

.footer-content {
     display: flex;
     justify-content: space-between;
     align-items: center;
     max-width: 1920px;
     margin: 0 auto;
     padding: 0 20px;
}

.footer-links a {
     color: #fff;
     margin: 0 10px;
     text-decoration: none;
}

.footer-links a:hover {
     text-decoration: underline;
}

.footer-rights p {
     margin: 0;
}

@media (max-width: 768px) {
     .footer-content {
          flex-direction: column;
     }

     .footer-links {
          margin-bottom: 10px;
     }
}